<template lang="html">
  <section class="products">
    <div class="col-lg-12 grid-margin stretch-card">
      <div class="card">
        <div class="card-body" style="padding-left: 15px; padding-right: 15px;">
          <div class="row">
            <div class="col-sm-6">
              <h3 class="card-title">List of Products</h3>
            </div>
            <div class="col-sm-6 text-right">
              <button class="btn btn-success" type="button" @click="openProductModal">Add Products</button>
            </div>
          </div>
          <div class="table-responsive">
            <div class="form-group">
              <input type="text" class="form-control" placeholder="Search Products" v-model="search"
                v-on:keyup="get_products()">
            </div>
            <table class="table text-center">
              <thead>
                <tr>
                  <th>ID</th>
                  <th>Name</th>
                  <th>Price</th>
                  <th>Stock</th>
                  <th>Status</th>
                  <th>Mode</th>
                  <th>Action</th>
                </tr>
              </thead>
              <tbody>
                <tr v-show="loader_spinner">
                  <th colspan="7" class="text-center">
                    <span class="spinner-border" role="status"></span>
                  </th>
                </tr>
                <tr v-show="!loader_spinner" v-for="(row, i) in list_products" :key="i">
                  <td>{{ row.product_id }}</td>
                  <td>{{ row.product_name }}</td>
                  <td>{{ row.product_price }}</td>
                  <td>{{ row.product_stock }}</td>
                  <td>{{ row.product_status }}</td>
                  <td>{{ (row.product_mode == 1) ? 'Manual' : 'Auto' }}</td>
                  <!-- <td>{{ (row.product_stock != null) ? 'Published' : 'Not Published' }}</td> -->
                  <td>
                    <button class="btn btn-info" data-toggle="modal"
                      @click="showUpdateProductModal(row.product_id, row.product_price, row.product_mode)">Update</button>
                    <button class="btn btn-danger" @click="unpublishedProducts(row.product_id)">Remove</button>
                  </td>
                </tr>
              </tbody>
              <tfoot>
                <tr>
                  <td class="page text-center" colspan="7" align="center">
                    <paginate :page-count="page_count" :container-class="'pagination text-center'"
                      page-class="page-item" prev-link-class="page-link" next-link-class="page-link"
                      page-link-class="page-link" prev-text="PREV" next-text="NEXT" :click-handler="get_products">
                    </paginate>
                  </td>
                </tr>
              </tfoot>
            </table>
          </div>
        </div>
      </div>
    </div>
    <modal name="productModal" :min-width="600" :min-height="400" :scrollable="true" width="80%" height="auto">
      <div class="modal-body" style="margin-bottom: 0px;">
        <div class="row center-block">
          <div class="col-md-12">
            <form>
              <div class="form-group">
                <div class="table-responsive">
                  <br><br><br>
                  <div class="form-group">
                    <input type="text" class="form-control" placeholder="Search Products" v-model="search_unpublished"
                      v-on:keyup="get_products_unpublished()">
                  </div>
                  <table class="table text-center">
                    <thead>
                      <tr>
                        <th>Select</th>
                        <th>Name</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-show="loader_spinner_add_products">
                        <th colspan="3" class="text-center">
                          <span class="spinner-border" role="status"></span>
                        </th>
                      </tr>
                      <tr v-show="!loader_spinner_add_products" v-for="(row, i) in list_products_unpublished" :key="i">
                        <td>
                          <div class="form-check form-check-inline">
                            <input type="checkbox" class="form-check-input" v-model="ListProductPublished"
                              :value="row.ProductID">
                          </div>
                        </td>
                        <td>{{ row.productName }}</td>
                      </tr>
                    </tbody>
                    <tfoot>
                      <tr>
                        <td class="page text-center" colspan="2" align="center" style="padding-bottom: 0px;">
                          <paginate :page-count="page_count_unpublished" :container-class="'pagination text-center'"
                            page-class="page-item" prev-link-class="page-link" next-link-class="page-link"
                            page-link-class="page-link" prev-text="PREV" next-text="NEXT"
                            :click-handler="get_products_unpublished" style=" margin-bottom:0px;"></paginate>
                        </td>
                      </tr>
                    </tfoot>
                  </table>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-default" @click="hideProductModal">Cancel</button>
        <button type="button" class="btn btn-success" @click="uploadProductBatch">Publish</button>
      </div>
    </modal>

    <modal name="updateProductModal" @before-open="beforeUpdateProductModalOpen" :height="250">
      <div class="modal-body">
        <div class="row center-block">
          <div class="col-md-12">
            <form>
              <div class="form-group">
                <div class="checkbox-inline" style="padding-left: 20px;">
                  <input type="checkbox" id="manualPricing" class="form-check-input" v-model="update_productMode"
                    :value="update_productMode">
                  <label class="form-check-label" for="manualPricing" style="padding-top:5px;">Manual Pricing</label>
                </div>
                <!--<div class="checkbox-inline" style="padding-left: 20px;">
                      <input type="checkbox" id="manualPricingWS" class="form-check-input" v-model="update_productModeWS" :value="update_productModeWS">
                      <label class="form-check-label" for="manualPricingWS" style="padding-top:5px;">Enable Wholesale Pricing</label>
                    </div>-->
                <br>
                <input type="hidden" class="form-control" id="updateProductID" v-model="update_productID">
                <label for="updateProductPrice">Price</label>
                <input type="text" class="form-control" id="updateProductPrice" v-model="update_productPrice"
                  :readonly="update_productMode == 0">
                <div v-if="update_productModeWS == 1">
                  <label for="updateProductPriceWS">Wholesale Price</label>
                  <input type="text" class="form-control" id="updateProductPriceWS" v-model="update_productPriceWS">
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-default" @click="hideUpdateProductModal">Cancel</button>
        <button type="button" class="btn btn-success" @click="updateProductFunc">Update</button>
      </div>
    </modal>
  </section>
</template>
<script lang="js">
import { proxy } from 'jquery'

/* eslint-disable */
export default {
  name: 'products',
  data() {
    return {
      search: '',
      search_unpublished: '',
      list_products: [],
      page_count: 0,
      page_count_unpublished: 0,
      prev: 'PREV',
      next: 'NEXT',
      currentSort: 'id',
      currentSortDir: 'desc',
      pagination: 'pagination',
      productModal: false,
      productPrice: 0,
      productStock: 0,
      productID: 0,
      update_productID: 0,
      update_productPrice: 0,
      update_productPriceWS: 0,
      update_productMode: 0,
      update_productModeWS: 0,
      // FOR PRODUCT BATCH UPLOAD ONLY
      file: '',
      btn_visibility: false,
      loader_spinner: true,
      loader_spinner_add_products: true,
      ListProductPublished: [],
      list_products_unpublished: []
    }
  },
  async created() {
    if (localStorage.getItem('user_role') == 2 || localStorage.getItem('user_role') == 4) {
      this.btn_visibility = true
    } else {
      this.btn_visibility = false
    }
  },
  methods: {
    async unpublishedProducts(prod_id) {
      try {
        this.$swal({
          title: 'Are you sure?',
          text: 'The product status will changed into unpublished.',
          type: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          confirmButtonText: 'Confirm'
        }).then(async (result) => {
          if (result.value) {
            let query = {
              product_id: prod_id,
              location_id: localStorage.getItem('location_id'),
              location_name: localStorage.getItem('location_name'),
              user_id: localStorage.getItem('user_id')
            }
            let row = await this.$axios.post(this.$apiUrl + "update_products_status", query, { proxy: { protocol: 'http', host: '192.168.0.196', port: 80 } })
            let res = row.data
            if (res.status) {
              this.$swal('Success!', 'Product is already unpublished', 'success')
              await this.get_products()
            } else {
              this.$swal('Error!', 'An error has occured.', 'error')
            }
          }
        })
      } catch (e) {

      }
    },
    async handleFileUpload() {
      this.file = this.$refs.file.files[0]
    },
    async uploadProductBatch() {
      try {
        let query = {
          list_of_ids: this.ListProductPublished,
          location_id: localStorage.getItem('location_id'),
          location_name: localStorage.getItem('location_name'),
          user_id: localStorage.getItem('user_id')
        }

        await this.$axios.post(this.$apiUrl + "update_products_unpublished", query, { proxy: { protocol: 'https', host: '192.168.0.203', port: 80 } })
        await this.get_products()
        this.$swal('Product Published!', 'You successfully published new products.')
        await this.hideProductModal()
      } catch (e) {
        this.$swal('Error', e.message, 'error')
      }
    },
    async updateProductDetails() {
      try {
        let query = {
          product_id: this.productID,
          product_price: this.productPrice,
          product_stock: this.productStock,
          location_id: localStorage.getItem('location_id'),
          location_name: localStorage.getItem('location_name'),
          user_id: localStorage.getItem('user_id')
        }
        let res = await this.$axios.post(this.$apiUrl + "update_product", query)
        await this.hideProductModal()
        await this.get_products()
        this.$swal('Product Updated!', 'You successfully updated a product')
      } catch (e) {
        this.$swal('Error', e.message, 'error')
      }
    },
    async updateProductFunc() {
      try {
        if (this.update_productMode == 1) {
          if (this.update_productPrice == 0) {
            this.$swal('Error', 'Product Price is less than or equal to 0.', 'error')
            return false
          }
        }

        if (this.update_productModeWS == 1) {
          if (this.update_productPriceWS == 0) {
            this.$swal('Error', 'Product Wholesale Price is less than or equal to 0.', 'error')
            return false
          }
        }

        if (this.update_productID == 0) {
          this.$swal('Error', 'Product ID is empty.', 'error')
          return false
        }

        let query = {
          product_id: this.update_productID,
          product_price: this.update_productPrice,
          product_price_ws: this.update_productPriceWS,
          product_mode: this.update_productMode,
          product_mode_ws: this.update_productModeWS,
          location_id: localStorage.getItem('location_id'),
          location_name: localStorage.getItem('location_name'),
          user_id: localStorage.getItem('user_id')
        }
        await this.$axios.post(this.$apiUrl + "update_product_excluded", query)
        await this.hideUpdateProductModal()
        await this.get_products()
        this.$swal('Product Updated!', 'You successfully updated a product')
      } catch (e) {
        this.$swal('Error', e.message, 'error')
      }
    },
    async hideUpdateProductModal() {
      this.update_productID = 0
      this.update_productPrice = 0
      this.update_productMode = 0
      this.update_productModeWS = 0
      this.$modal.hide('updateProductModal')
    },
    async showUpdateProductModal(productID, productPrice, productMode, productModeWS) {
      this.$modal.show('updateProductModal', { productID: productID, productPrice: productPrice, productMode: productMode, productModeWS: productModeWS })
    },
    async beforeUpdateProductModalOpen(event) {
      this.update_productID = event.params.productID
      this.update_productPrice = event.params.productPrice
      this.update_productMode = event.params.productMode
      this.update_productModeWS = event.params.productModeWS
    },
    async openProductModal() {
      this.$modal.show('productModal')
      await this.get_products_unpublished()
    },
    async hideProductModal() {
      this.list_products_unpublished = []
      this.ListProductPublished = []
      this.$modal.hide('productModal')
    },
    async pageChanged(page) {
      await this.get_products(page)
    },
    async get_products(page = 1) {
      this.loader_spinner = true
      try {
        let query = {
          p_page: page,
          p_search: this.search,
          p_currentSort: this.currentSort,
          p_currentSortDir: this.currentSortDir
        }

        let result = await this.$axios.post(this.$apiUrl + "get_products", query)
        let res = result.data
        this.list_products = res.res
        this.page_count = parseInt(res.page_count)
      } catch (e) {
        this.$swal('Error', e.message, 'error')
      }
      this.loader_spinner = false
    },
    async get_products_unpublished(page = 1) {
      this.loader_spinner_add_products = true
      try {
        let query = {
          p_page: page,
          p_search: this.search_unpublished,
          p_currentSort: this.currentSort,
          p_currentSortDir: this.currentSortDir
        }

        let result = await this.$axios.post(this.$apiUrl + "get_products_unpublished", query)
        let res = result.data
        this.list_products_unpublished = res.res
        this.page_count_unpublished = parseInt(res.page_count)
      } catch (e) {
        this.$swal('Error', e.message, 'error')
      }
      this.loader_spinner_add_products = false
    }
  },
  async mounted() {
    await this.get_products()
  }
}
</script>

<style scoped lang="scss">
.pagination {
  display: flex;
  justify-content: center;
}
</style>


