<template lang="html">
    <section class="orders">
        <div class="col-lg-12 grid-margin stretch-card">
            <div class="card">
                <div class="card-body" style="padding-left: 15px; padding-right: 15px;">
                    <h3 class="card-title">List of Completed Orders</h3>
                    <div class="table-responsive">
                        <!-- <div class="form-group">
                    <input type="text" class="form-control" placeholder="Search Products" v-model="search" v-on:keyup="get_orders()">
                </div> -->
                        <table class="table text-center">
                            <thead>
                                <tr>
                                    <th>Order No</th>
                                    <th>Customer Name</th>
                                    <th>Mode of Payment</th>
                                    <th>Date Ordered</th>
                                    <th>Total Amount</th>
                                    <th>Status</th>
                                    <th>Action</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-show="loader_spinner">
                                    <th colspan="7" class="text-center">
                                        <span class="spinner-border" role="status"></span>
                                    </th>
                                </tr>
                                <tr v-show="!loader_spinner" v-for="(row, i) in list_orders" :key="i">
                                    <td>{{ row.order_id }}</td>
                                    <td>{{ row.first_name + ' ' + row.last_name }}</td>
                                    <td>{{ row.payment_method_title }}</td>
                                    <td>{{ row.date_created_gmt | laymandate }}</td>
                                    <td>{{ row.total_amount }}</td>
                                    <td>{{ row.status | wpstatus }}</td>
                                    <td>
                                        <button class="btn btn-primary" @click="openOrderModal(row)">View</button>
                                    </td>
                                </tr>
                            </tbody>
                            <tfoot>
                                <tr>
                                    <td class="page text-center" colspan="7" align="center">
                                        <paginate :pageCount="page_count" :container-class="'pagination text-center'"
                                            page-class="page-item" prev-link-class="page-link"
                                            next-link-class="page-link" page-link-class="page-link" prev-text="PREV"
                                            next-text="NEXT" :click-handler="get_orders"></paginate>
                                    </td>
                                </tr>
                            </tfoot>
                        </table>
                    </div>
                </div>
            </div>
        </div>
        <modal id="orderModal" name="orderModal" @before-open="beforeOrderModalOpen" height="auto" :width="900"
            :scrollable="true">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title">Order #{{ orderDetails.order_id }}</h5>
                </div>
                <div class="modal-body">
                    <div class="row center-block">
                        <div class="col-md-12">
                            <div class="row">
                                <div class="col-md-4">
                                    <p class="h6">
                                        <strong>Order Status</strong>
                                    </p>
                                    <div class="form-inline">
                                        <div class="form-group">
                                            <label for="orderStatus" style="padding-right: 10px;">Status</label>
                                            <select id="orderStatus" v-model="orderDetails.status" class="form-control"
                                                style="width: 150px;">
                                                <option value="wc-processing">Processing</option>
                                                <option value="wc-completed">Completed</option>
                                                <option value="wc-cancelled">Cancelled</option>
                                            </select>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-md-6">
                                    <p class="h6">
                                        <strong>Customer Details</strong>
                                    </p>
                                    <div class="form-group">
                                        <p style="margin-bottom:8px;"><strong>Customer Name:</strong> {{
                                            orderDetails.first_name + ' ' + orderDetails.last_name }}</p>
                                        <p style="margin-bottom:8px;"><strong>Customer Address:</strong> {{
                                            orderDetails.address_1 + ' ' + ((orderDetails.address_2 == null) ? '' :
                                            orderDetails.address_2) + ' ' + orderDetails.city }}</p>
                                        <p style="margin-bottom:8px;"><strong>Customer Contact#:</strong> {{
                                            orderDetails.phone }}</p>
                                    </div>
                                </div>
                            </div>
                            <div class="row">
                                <div class="col-sm-12 col-md-12 col-lg-12">
                                    <strong>List of Items</strong>
                                    <div class="table-responsive">
                                        <table class="table text-center">
                                            <thead>
                                                <tr>
                                                    <th>Item</th>
                                                    <th>Price</th>
                                                    <th>Quantity</th>
                                                    <th>Total</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr v-show="loader_spinner_items">
                                                    <th colspan="4" class="text-center">
                                                        <span class="spinner-border" role="status"></span>
                                                    </th>
                                                </tr>
                                                <tr v-show="!loader_spinner_items" v-for="(row, i) in orderItems"
                                                    :key="i">
                                                    <td>{{ row.order_item_name }}</td>
                                                    <td>{{ parseFloat(row.lineTotal / row.Qty) }}</td>
                                                    <td>{{ row.Qty }}</td>
                                                    <td>{{ row.subtotal }}</td>
                                                </tr>
                                            </tbody>
                                            <tfoot>
                                                <!-- <tr>
                                                    <td class="text-right" colspan="3">
                                                        <strong>Item Subtotal:</strong>
                                                    </td>
                                                    <td colspan="1">
                                                        69.15
                                                    </td>
                                                </tr> -->
                                                <tr>
                                                    <td class="text-right" colspan="3">
                                                        <strong>Order Total:</strong>
                                                    </td>
                                                    <td colspan="1">
                                                        {{ orderTotalPrice }}
                                                    </td>
                                                </tr>
                                            </tfoot>
                                        </table>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-default" @click="hideOrderModal">Cancel</button>
                    <button type="button" class="btn btn-success"
                        @click="updateOrderDetails(orderDetails.order_id, orderDetails.status)">Update</button>
                </div>
            </div>
        </modal>
    </section>
</template>
<script lang="js">
/* eslint-disable */
export default {
    name: 'completed_orders',
    data() {
        return {
            // search: '',
            list_orders: [],
            page_count: 0,
            prev: 'PREV',
            next: 'NEXT',
            currentSort: 'id',
            currentSortDir: 'desc',
            pagination: 'pagination',
            orderDetails: [],
            orderItems: [],
            orderTotalPrice: 0,
            loader_spinner: true,
            loader_spinner_items: true
        }
    },
    methods: {
        async updateOrderDetails(order_id, status) {
            await this.update_order(order_id, status)
        },
        async beforeOrderModalOpen(event) {
            this.orderDetails = event.params.order_details
        },
        async openOrderModal(row) {
            this.$modal.show('orderModal', { order_details: row })
            await this.get_order_items(row.order_id)
        },
        async hideOrderModal() {
            this.orderDetails = []
            this.orderItems = []
            this.orderTotalPrice = 0
            this.$modal.hide('orderModal')
        },
        async pageChanged(page) {
            await this.get_orders(page)
        },
        async get_orders(page = 1) {
            this.loader_spinner = true
            try {
                let query = {
                    // p_search: this.search,
                    p_page: page,
                    p_currentSort: this.currentSort,
                    p_currentSortDir: this.currentSortDir,
                    p_orderType: 'wc-completed'
                }

                let result = await this.$axios.post(this.$apiUrl + "get_orders", query)
                let res = result.data
                this.list_orders = res.res
                this.page_count = parseInt(res.page_count)
            } catch (e) {
                this.$swal('Error', e.message, 'error')
            }
            this.loader_spinner = false
        },
        async get_order_items(order_id) {
            this.loader_spinner_items = true
            try {
                let query = {
                    order_id: order_id
                }

                let result = await this.$axios.post(this.$apiUrl + "get_order_items", query)
                let res = result.data
                this.orderItems = res
            } catch (e) {
                this.$swal('Error', e.message, 'error')
            }
            this.loader_spinner_items = false
        },
        async update_order(order_id, status) {
            try {
                let query = {
                    order_id: order_id,
                    status: status
                }

                await this.$axios.post(this.$apiUrl + "update_order", query)
                await this.get_orders()
                this.$swal('Order Updated!', 'You successfully updated an order')
                this.$modal.hide('orderModal')
            } catch (e) {
                this.$swal('Error', e.message, 'error')
            }
        }
    },
    async mounted() {
        await this.get_orders()
    },
    watch: {
        orderItems: {
            handler: function () {
                this.orderTotalPrice = 0
                this.orderItems.forEach(p => {
                    this.orderTotalPrice = parseFloat(this.orderTotalPrice) + parseFloat(p.subtotal)
                })
            }, deep: true
        }
    }
}
</script>

<style scoped lang="scss">
.pagination {
    display: flex;
    justify-content: center;
}

#orderModal {
    z-index: 10000000 !important;
}
</style>


