<template lang="html">

  <section class="tables">
    <div class="row">
      <div class="col-lg-12 grid-margin stretch-card">
        <div class="card">
          <div class="card-body">
            <div class="row">
              <div class="col-sm-6">
                <h3 class="card-title">List of Users</h3>
              </div>
              <div class="col-sm-6 text-right">
                <button class="btn btn-success" type="button" @click="openAddUserModal">Add User</button>
              </div>
            </div>
            <div class="table-responsive">
              <div class="form-group">
                <input type="text" class="form-control" placeholder="Search Users" v-model="search"
                  v-on:keyup="get_users()">
              </div>
              <table class="table text-center">
                <thead>
                  <tr>
                    <th>ID</th>
                    <th>Username</th>
                    <th>Location</th>
                    <th>Role</th>
                    <th>Action</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-show="loader_spinner">
                    <th colspan="5" class="text-center">
                      <span class="spinner-border" role="status"></span>
                    </th>
                  </tr>
                  <tr v-show="!loader_spinner" v-for="(row, i) in list_users" :key="i">
                    <td>{{ row.id }}</td>
                    <td>{{ row.username }}</td>
                    <td>{{ row.location_name }}</td>
                    <td>{{ row.user_role_details }}</td>
                    <td>
                      <button class="btn btn-primary" data-toggle="modal"
                        @click="openUserModal(row.id, row.username, row.password, row.user_role)">View</button>
                    </td>
                  </tr>
                </tbody>
                <tfoot>
                  <tr>
                    <td class="page text-center" colspan="5" align="center">
                      <paginate :page-count="page_count" :container-class="'pagination text-center'"
                        page-class="page-item" prev-link-class="page-link" next-link-class="page-link"
                        page-link-class="page-link" prev-text="PREV" next-text="NEXT" :click-handler="get_users">
                      </paginate>
                    </td>
                  </tr>
                </tfoot>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>

    <modal name="AdduserModal" :height="220">
      <div class="modal-body">
        <div class="row center-block">
          <div class="col-md-12">
            <form ref="addUserForm">
              <!-- <div class="form-group row">
                  <label for="userRole" class="col-sm-2 col-form-label">User Role</label>
                  <div class="col-sm-10">
                    <select class="form-control" name="user_role" id="user_role" v-model="user_role">
                      <option disabled selected value="0">-- USER ROLE --</option>
                      <option value="1">Peddler</option>
                      <option value="2">Franchisee</option>
                      <option value="3">Compal</option>
                      <option value="4">Admin</option>
                    </select>
                  </div>
              </div> -->
              <!-- <div class="form-group row">
                  <label for="branch" class="col-sm-2 col-form-label">Branch</label>
                  <div class="col-sm-10">
                    <select class="form-control" name="new_user_branch" id="new_user_branch" v-model="new_user_branch" @change="userRoleChange">
                      <option disabled selected value="">-- BRANCH --</option>
                      <option v-for="(row,i) in list_branch" :key="i" :value="row.db_133">{{ row.name }}</option>
                    </select>
                  </div>
              </div> -->
              <!-- <div class="form-group row" v-show="showUserFullname">
                  <label for="user_full_name" class="col-sm-2 col-form-label">Account No.</label>
                  <div class="col-sm-8">
                    <input type="text" class="form-control" v-model="new_user_account_no">
                  </div>
                  <div class="col-sm-2 text-center">
                    <button type="button" class="btn btn-success form-control" @click="searchAccountNO"><i class="mdi mdi-magnify" style="font-size: 25px; margin: 0px;" ></i></button>
                  </div>
              </div> -->
              <div class="form-group row">
                <label for="username" class="col-sm-2 col-form-label">Username</label>
                <div class="col-sm-10">
                  <input type="text" class="form-control" v-model="user_name">
                  <input type="text" class="form-control" hidden="true" v-model="user_firstname">
                  <input type="text" class="form-control" hidden="true" v-model="user_lastname">
                </div>
              </div>
              <div class="form-group row">
                <label for="password" class="col-sm-2 col-form-label">Password</label>
                <div class="col-sm-10">
                  <input type="password" class="form-control" v-model="password">
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-default" @click="hideAddUserModal">Cancel</button>
        <button type="button" class="btn btn-success" @click="add_user">Add</button>
      </div>
    </modal>

    <modal name="userModal" @before-open="beforeUserModalOpen" :height="300">
      <div class="modal-body">
        <div class="row center-block">
          <div class="col-md-12">
            <form>
              <div class="form-group row">
                <label for="id" class="col-sm-2 col-form-label">User ID</label>
                <div class="col-sm-10">
                  <input type="text" readonly class="form-control" v-model="user_id">
                </div>
              </div>
              <div class="form-group row">
                <label for="username" class="col-sm-2 col-form-label">Username</label>
                <div class="col-sm-10">
                  <input type="text" readonly class="form-control" v-model="user_name">
                </div>
              </div>
              <div class="form-group row">
                <label for="password" class="col-sm-2 col-form-label">Password</label>
                <div class="col-sm-10">
                  <input type="password" id="password" class="form-control" aria-describedby="passwordHelpBlock"
                    v-model="password">
                  <small id="passwordHelpBlock" class="form-text text-muted">Leave as is if you don't want to change
                    it.</small>
                </div>
              </div>
              <!-- <h6>User Role</h6>
                <div class="row text-center">
                  <div class="col-sm-3">
                    <div class="form-check form-check-inline">
                      <input type="radio" name="roleRadio" class="form-check-input" value="1" id="roleRadio1" v-model="user_role">
                      <label for="form-check-label" form="roleRadio1">Peddler</label>
                    </div>
                  </div>
                  <div class="col-sm-3">
                    <div class="form-check form-check-inline">
                      <input type="radio" name="roleRadio" class="form-check-input" value="2" id="roleRadio2" v-model="user_role">
                      <label for="form-check-label" form="roleRadio2">Franchisee</label>
                    </div>
                  </div>
                  <div class="col-sm-3">
                    <div class="form-check form-check-inline">
                      <input type="radio" name="roleRadio" class="form-check-input" value="3" id="roleRadio3" v-model="user_role">
                      <label for="form-check-label" form="roleRadio3">Compal</label>
                    </div>
                  </div>
                  <div class="col-sm-3">
                    <div class="form-check form-check-inline">
                      <input type="radio" name="roleRadio" class="form-check-input" value="4" id="roleRadio4" v-model="user_role">
                      <label for="form-check-label" form="roleRadio4">Admin</label>
                    </div>
                  </div>
                </div> -->
            </form>
          </div>
        </div>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-default" @click="hideUserModal">Cancel</button>
        <button type="button" class="btn btn-success" @click="update_user">Update</button>
      </div>
    </modal>
  </section>
</template>
<script lang="js">
/* eslint-disable */
export default {
  name: 'users',
  data() {
    return {
      search: '',
      list_users: [],
      page_count: 0,
      user_id: 0,
      user_name: '',
      password: '',
      user_role: 0,
      new_user_branch: '',
      list_branch: [],
      new_user_account_no: '',
      showUserFullname: false,
      readOnlyUsername: true,
      loader_spinner: true,
      user_firstname: '',
      user_lastname: '',
    }
  },
  methods: {
    async searchAccountNO() {
      try {
        if (this.new_user_account_no == '') {
          this.$swal('Error!', 'Account No. is empty.', 'error')
          return false
        }
        let query = {
          branch: this.new_user_branch,
          account_no: this.new_user_account_no
        }
        let result = await this.$axios.post(this.$apiUrl + "get_user_details", query)
        let res = result.data
        if (res.status == false) {
          this.$swal('Error', res.res, 'error')
          return false
        }
        this.$swal('Success', 'Account details found')
        this.user_name = res.res.customercode
        this.password = res.res.last_name.toUpperCase()
        this.user_firstname = res.res.first_name.toUpperCase()
        this.user_lastname = res.res.last_name.toUpperCase()
      } catch (e) {
        this.$swal('Error', e.message, 'error')
      }
    },
    async list_branches() {
      try {
        let result = await this.$axios.get(this.$apiUrl + "get_branches")
        let res = result.data
        this.list_branch = res.res
      } catch (e) {
        this.$swal('Error', e.message, 'error')
      }
    },
    async userRoleChange() {
      if (this.user_role == 1) {
        this.new_user_account_no = ''
        this.showUserFullname = true
        this.readOnlyUsername = true
      } else {
        this.showUserFullname = false
        this.readOnlyUsername = false
      }
    },
    async add_user() {
      try {
        let query = {
          username: this.user_name,
          userpass: this.password,
          userrole: this.user_role,
          userbranch: this.new_user_branch,
          userfirstname: this.user_firstname,
          userlastname: this.user_lastname
        }

        let result = await this.$axios.post(this.$apiUrl + "add_user", query)
        let res = result.data
        if (res == true) {
          this.hideAddUserModal()
          await this.get_users()
          this.$swal('Account Added!', 'You successfully added an account')
        } else {
          this.$swal('Error', res.res, 'error')
        }
      } catch (e) {
        this.$swal('Error', e.message, 'error')
      }
    },
    async update_user() {
      try {
        let query = {
          user_id: this.user_id,
          password: this.password,
          user_role: this.user_role
        }

        let result = await this.$axios.post(this.$apiUrl + "update_user", query)
        let res = result.data
        if (res == true) {
          this.hideUserModal()
          await this.get_users()
          this.$swal('Account Updated!', 'You successfully updated an account')
        }
      } catch (e) {
        this.$swal('Error', e.message, 'error')
      }
    },
    async get_users(page = 1) {
      this.loader_spinner = true
      try {
        let query = {
          p_page: page,
          p_search: this.search,
          p_currentSort: 'id',
          p_currentSortDir: 'desc'
        }

        let result = await this.$axios.post(this.$apiUrl + "get_users", query)
        let res = result.data
        this.list_users = res.res
        this.page_count = parseInt(res.page_count)
      } catch (e) {
        this.$swal('Error', e.message, 'error')
      }
      this.loader_spinner = false
    },
    async beforeUserModalOpen(event) {
      this.user_id = event.params.user_id
      this.user_name = event.params.user_name
      this.password = event.params.password
      this.user_role = event.params.user_role
    },
    async openAddUserModal() {
      // await this.list_branches()
      this.$modal.show('AdduserModal')
    },
    async openUserModal(user_id, user_name, password, user_role) {
      this.$modal.show('userModal', { user_id: user_id, user_name: user_name, password: password, user_role: user_role })
    },
    async hideAddUserModal() {
      this.user_role = 0
      this.user_name = ''
      this.password = ''
      this.new_user_branch = ''
      this.user_firstname = ''
      this.user_lastname = ''
      this.new_user_account_no = ''
      this.showUserFullname = false
      this.$modal.hide('AdduserModal')
    },
    async hideUserModal() {
      this.user_id = 0
      this.user_name = ''
      this.password = ''
      this.user_role = 0
      this.$modal.hide('userModal')
    },
  },
  async mounted() {
    await this.get_users()
  }
}
</script>

<style scoped lang="scss">
.pagination {
  display: flex;
  justify-content: center;
}
</style>



