<template lang="html">
  <section class="app-sidebar">
    <nav class="sidebar sidebar-offcanvas" id="sidebar">
      <ul class="nav">
        <li class="nav-item nav-profile">
          <a href="#" class="nav-link">
            <div class="text-wrapper">
              <p class="profile-name">{{ first_name + ' ' + last_name }}</p>
            </div>
          </a>
        </li>
        <!-- <li class="nav-item">
          <router-link class="nav-link" to="/dashboard"><span class="menu-title">SRS Dashboard</span></router-link>
        </li> -->
        <li class="nav-item" v-if="isProducts">
          <a class="nav-link" v-b-toggle="'products'">
            <i class="menu-icon mdi mdi-table"></i>
            <span class="menu-title">Website Products</span>
            <i class="menu-arrow"></i>
          </a>
          <b-collapse id="products">
            <ul class="nav flex-column sub-menu">
              <li class="nav-item">
                <router-link class="nav-link" to="/products"><span class="menu-title">List of
                    Products</span></router-link>
              </li>
              <li class="nav-item">
                <router-link class="nav-link" to="/products/oos_products"><span class="menu-title">Out of
                    Stock</span></router-link>
              </li>
            </ul>
          </b-collapse>
        </li>
        <li class="nav-item" v-if="isOrders">
          <a class="nav-link" v-b-toggle="'orders'">
            <i class="menu-icon mdi mdi-table"></i>
            <span class="menu-title">Website Orders</span>
            <i class="menu-arrow"></i>
          </a>
          <b-collapse id="orders">
            <ul class="nav flex-column sub-menu">
              <li class="nav-item">
                <router-link class="nav-link" to="/orders/pending_orders"><span class="menu-title">Processing
                    Orders</span></router-link>
              </li>
              <li class="nav-item">
                <router-link class="nav-link" to="/orders/completed_orders"><span class="menu-title">Completed
                    Orders</span></router-link>
              </li>
              <li class="nav-item">
                <router-link class="nav-link" to="/orders/cancelled_orders"><span class="menu-title">Cancelled
                    Orders</span></router-link>
              </li>
            </ul>
          </b-collapse>
        </li>
        <!-- <li class="nav-item" v-if="isUsers">
          <router-link class="nav-link" to="/users/"><span class="menu-title">Users</span></router-link>
        </li> -->
        <li class="nav-item">
          <span class="nav-link">
            <a href="#" @click="logout()" class="menu-title">Logout</a>
          </span>
        </li>
      </ul>
    </nav>
  </section>
</template>

<script lang="js">
/* eslint-disable */
import JQuery from 'jquery'
let $ = JQuery
export default {
  name: 'app-sidebar',
  data() {
    return {
      isProducts: false,
      isOrders: false,
      isUsers: false,
      first_name: '',
      last_name: ''
    }
  },
  async created() {
    this.first_name = localStorage.getItem('user_firstname')
    this.last_name = localStorage.getItem('user_lastname')
    if (localStorage.getItem('user_role') == 4) {
      this.isProducts = true
      this.isOrders = true
      this.isUsers = true
    } else {
      this.isProducts = true
      this.isOrders = true
    }
  },
  mounted() {
    const body = document.querySelector('body')
    // add class 'hover-open' to sidebar navitem while hover in sidebar-icon-only menu
    document.querySelectorAll('.sidebar .nav-item').forEach(function (el) {
      el.addEventListener('mouseover', function () {
        if (body.classList.contains('sidebar-icon-only')) {
          el.classList.add('hover-open')
        }
      })
      el.addEventListener('mouseout', function () {
        if (body.classList.contains('sidebar-icon-only')) {
          el.classList.remove('hover-open')
        }
      })
    })
  },
  methods: {
    async logout() {
      localStorage.removeItem('token')
      localStorage.removeItem('account_no')
      localStorage.removeItem('branch')
      localStorage.removeItem('user_role')
      localStorage.removeItem('user_id')
      localStorage.removeItem('user_firstname')
      localStorage.removeItem('user_lastname')
      this.$router.push({ name: 'login' })
    }
  }
}
</script>

<style scoped lang="scss">
.app-sidebar {}
</style>



