<template lang='html'>
  <section class='dashboard_order'>
    <div class='row page-title-header'>
      <div class='col-12'>
        <div class='page-header'>
          <router-link to="/dashboard"><i class="mdi mdi-keyboard-backspace"
              style="padding-right: 10px; font-size: 20px;"></i></router-link>
          <h4 class='page-title'>View Order #{{ transactionNo }}</h4>
        </div>
      </div>
    </div>
    <div class='row'>
      <div class="col-md-12 grid-margin stretch-card">
        <div class="card">
          <div class="card-body">
            <h4 class='card-title mb-0'>List of Items</h4>
            <div class="table-responsive">
              <table class="table text-center">
                <thead>
                  <tr>
                    <th>Description</th>
                    <th>Barcode</th>
                    <th>UOM</th>
                    <th>Price</th>
                    <th>Total Quantity</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(row, i) in list_transaction_details" :key="i">
                    <td>{{ row.Description }}</td>
                    <td>{{ row.Barcode }}</td>
                    <td>{{ row.UOM }}</td>
                    <td>{{ parseFloat(row.Price).toFixed(2) }}</td>
                    <td>{{ row.TotalQty }}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>
<script lang='js'>
/* eslint-disable */
import JQuery from 'jquery'
let $ = JQuery
export default {
  name: 'dashboard_order',
  data() {
    return {
      transactionNo: '',
      list_transaction_details: []
    }
  },
  async created() {
    this.transactionNo = this.$route.params.transaction_no
    this.list_transaction_details = this.$route.params.list_details
  },
  methods: {

  }
}
</script>

<style scoped lang='scss'></style>


