<template lang='html'>
  <section class='dashboard'>
    <div class='row page-title-header'>
      <div class='col-12'>
        <div class='page-header'>
          <h4 class='page-title'>Dashboard</h4>
        </div>
      </div>
    </div>
    <div class="row" v-show="loading_data">
      <div class="col-md-12 text-center">
        <span class="spinner-border" role="status"></span>
      </div>
    </div>
    <div class='row' v-show="!loading_data">
      <div class='col-md-3 grid-margin stretch-card'>
        <div class='card'>
          <div class='card-body'>
            <h4 class='card-title mb-0'>Orders this Month</h4>
            <div class='d-flex py-2 border-bottom'>
              <div class='wrapper'>
                <p class='font-weight-semibold text-gray mb-0' style="font-size: 25px;">{{ ordersCountMonth }}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class='col-md-3 grid-margin stretch-card'>
        <div class='card'>
          <div class='card-body'>
            <h4 class='card-title mb-0'>Orders Amount this Month</h4>
            <div class='d-flex py-2 border-bottom'>
              <div class='wrapper'>
                <p class='font-weight-semibold text-gray mb-0' style="font-size: 25px;">{{ ordersAmountMonth | money }}
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class='col-md-3 grid-margin stretch-card'>
        <div class='card'>
          <div class='card-body'>
            <h4 class='card-title mb-0'>Total Orders Count</h4>
            <div class='d-flex py-2 border-bottom'>
              <div class='wrapper'>
                <p class='font-weight-semibold text-gray mb-0' style="font-size: 25px;">{{ allordersCount }}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class='col-md-3 grid-margin stretch-card'>
        <div class='card'>
          <div class='card-body'>
            <h4 class='card-title mb-0'>Total Orders Amount</h4>
            <div class='d-flex py-2 border-bottom'>
              <div class='wrapper'>
                <p class='font-weight-semibold text-gray mb-0' style="font-size: 25px;">{{ allordersAmount | money }}
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="row" v-show="!loading_data">
      <div class='col-md-12 grid-margin stretch-card'>
        <div class='card'>
          <div class='card-body'>
            <h4 class='card-title mb-0'>List of Transactions</h4>
            <div class="table-responsive">
              <div class="form-group">
                <!-- <input type="text" class="form-control" placeholder="Search Transaction" v-model="search" v-on:keyup="get_products()"> -->
              </div>
              <table class="table text-center">
                <thead>
                  <tr>
                    <th>Transaction No.</th>
                    <th>Amount</th>
                    <th>Description</th>
                    <th>Date of Transaction</th>
                    <th>Action</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(row, i) in list_transactions" :key="i">
                    <td>{{ row.TransactionNo }}</td>
                    <td>{{ row.Amount | money }}</td>
                    <td>{{ row.Description }}</td>
                    <td>{{ row.LogDate | hoursago }}</td>
                    <td><button type="button" class="btn btn-success"
                        @click="viewTransaction(row.TransactionNo, row.TerminalNo)">View</button></td>
                    <!-- <td><button class="btn btn-success" style="margin-right: 5px;" v-on:click="viewOrder(row.TransactionNo)">View</button></td> -->
                  </tr>
                </tbody>
                <tfoot>
                  <tr>
                    <td class="page text-center" colspan="5" align="center">
                      <paginate :pageCount="page_count" :container-class="'pagination text-center'"
                        page-class="page-item" prev-link-class="page-link" next-link-class="page-link"
                        page-link-class="page-link" prev-text="PREV" next-text="NEXT" :click-handler="get_transactions">
                      </paginate>
                    </td>
                  </tr>
                </tfoot>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
    <modal name="viewTransactionModal" :min-width="200" :min-height="200" :max-height="600" :scrollable="true"
      :reset="true" width="80%" height="auto">
      <div class="modal-body">
        <div class="row center-block">
          <div class="col-md-12">
            <div class="table-responsive">
              <table class="table text-center">
                <thead>
                  <tr>
                    <th>Description</th>
                    <th>Barcode</th>
                    <th>UOM</th>
                    <th>Price</th>
                    <th>Total Quantity</th>
                    <th>Points</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(row, i) in list_transaction_details" :key="i">
                    <td>{{ row.Description }}</td>
                    <td>{{ row.Barcode }}</td>
                    <td>{{ row.UOM }}</td>
                    <td>{{ row.Price }}</td>
                    <td>{{ row.TotalQty }}</td>
                    <td>{{ row.Points }}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-danger" @click="hideviewTransactionModal">Close</button>
      </div>
    </modal>
  </section>
</template>

<script lang='js'>
/* eslint-disable */
import JQuery from 'jquery'
let $ = JQuery
export default {
  name: 'dashboard',
  data() {
    return {
      account_no: localStorage.getItem('account_no'),
      branch: localStorage.getItem('branch'),
      ordersCountMonth: 0,
      ordersAmountMonth: 0,
      allordersCount: 0,
      allordersAmount: 0,
      list_transactions: [],
      list_transaction_details: [],
      page_count: 0,
      loading_data: true
    }
  },
  methods: {
    async viewOrder(i) {
      this.$router.push({ name: 'DashboardOrder', params: { TransactionNo: i, ListDetails: this.list_transaction_details } })
      // const { href } = this.$router.resolve({ name: 'dashboard_order', params: { TransactionNo: i, ListDetails: this.list_transaction_details } })
      // window.open(href, '_blank')
    },
    async hideviewTransactionModal() {
      this.list_transaction_details = []
      this.$modal.hide('viewTransactionModal')
    },
    async get_transactions(page = 1) {
      try {
        let query = {
          p_page: page,
          p_currentSort: 'LogDate',
          p_currentSortDir: 'desc'
        }

        let result = await this.$axios.get(this.$apiUrl + "get_list_of_transactions", { params: query })
        let res = result.data
        this.list_transactions = res.res
        this.page_count = parseInt(res.page_count)
        this.loading_data = false
      } catch (e) {
        this.$swal('Error', e.message, 'error')
      }
    },
    async viewTransaction(TransactionNo, TerminalNo) {
      // await this.get_transactions()
      try {
        let query = {
          TransactionNo: TransactionNo,
          TerminalNo: TerminalNo,
          branch: this.branch
        }

        let result = await this.$axios.get(this.$apiUrl + "get_transaction_details", { params: query })
        let res = result.data
        this.list_transaction_details = res.res
        this.$router.push({ name: 'DashboardOrder', params: { transaction_no: TransactionNo, list_details: this.list_transaction_details } })
      } catch (e) {
        this.$swal('Error', e.message, 'error')
      }
    },
    async getAllDetails() {
      try {
        let query = {
          account_no: this.account_no,
          branch: this.branch
        }

        this.$axios.all([
          await this.$axios.get(this.$apiUrl + "get_total_order_count_month", { params: query }),
          await this.$axios.get(this.$apiUrl + "get_total_order_amount_month", { params: query }),
          await this.$axios.get(this.$apiUrl + "get_total_order_count", { params: query }),
          await this.$axios.get(this.$apiUrl + "get_total_order_amount", { params: query }),
        ]).then(this.$axios.spread((get_total_order_count_month, get_total_order_amount_month, get_total_order_count, get_total_order_amount) => {
          this.ordersCountMonth = get_total_order_count_month.data.row
          this.ordersAmountMonth = get_total_order_amount_month.data.row
          this.allordersCount = get_total_order_count.data.row
          this.allordersAmount = get_total_order_amount.data.row
        })).catch((e) => {
          this.$swal('Error', e.message, 'error')
        })

      } catch (e) {
        console.log(e)
      }
    }
  },
  async mounted() {
    await this.getAllDetails()
    await this.get_transactions()
  }
}
</script>

<style scoped lang='scss'></style>



