/* eslint-disable */

import Vue from 'vue'
import Router from 'vue-router'

import dashboard from '../views/dashboard'
import DashboardOrder from '../views/dashboard_order.vue'
import mainView from '../mainView'
import login from '../views/user-pages/login'
import products from '../views/products/list_products'
import oos_products from '../views/products/list_oos'
import pending_orders from '../views/orders/pending_orders'
import completed_orders from '../views/orders/completed_orders'
import cancelled_orders from '../views/orders/cancelled_orders'

import users from '../views/users/list_users'

Vue.use(Router)

const routes = [{
  path: '/',
  component: login,
  name: 'login'
},
{
  path: '/products',
  component: mainView,
  meta: { requiresAuth: true },
  children: [
    // {
    //   path: '/dashboard',
    //   name: 'dashboard',
    //   component: dashboard
    // },
    {
      path: '/dashboard/view_order/:transaction_no/:list_details',
      name: 'DashboardOrder',
      component: DashboardOrder
    },
    {
      path: '/products',
      name: 'products',
      component: products
    },
    {
      path: '/products/oos_products',
      name: 'oos_products',
      component: oos_products
    },
    {
      path: '/orders/pending_orders',
      name: 'pending_orders',
      component: pending_orders
    },
    {
      path: '/orders/completed_orders',
      name: 'completed_orders',
      component: completed_orders
    },
    {
      path: '/orders/cancelled_orders',
      name: 'cancelled_orders',
      component: cancelled_orders
    },
    {
      path: '/users',
      name: 'users',
      component: users
    },
  ]
},
{
  path: '*',
  redirect: '/pages/error_404',
  component: {
    render (c) { return c('router-view') }
  }}
]


const router = new Router({
  linkActiveClass: 'active',
  scrollBehavior: () => ({ y: 0 }),
  mode: 'history',
  routes
})

router.beforeEach((to, from, next) => {
  if(to.matched.some((route) => route.meta.requiresAuth)) {
    const token = localStorage.getItem('token')
    if(!token) {
      localStorage.removeItem('token')
      localStorage.removeItem('location_id')
      localStorage.removeItem('location_name')
      localStorage.removeItem('user_role')
      localStorage.removeItem('user_id')
      return next('/')
    }
    const tokenPayload = JSON.parse(window.atob(token.split('.')[1]))
    const isExpired = Date.now() >= tokenPayload.exp * 1000
    if(token && !isExpired) {
      next()
    } else {
      localStorage.removeItem('token')
      localStorage.removeItem('location_id')
      localStorage.removeItem('location_name')
      localStorage.removeItem('user_role')
      next('/')
    }
  } else {
    next()
  }
})


export default router;


