<template lang="html">

  <section class="login">
    <div class="container-scroller">
      <div class="container-fluid page-body-wrapper full-page-wrapper">
        <div class="content-wrapper d-flex align-items-center auth auth-bg-1 theme-one">
          <div class="row w-100">
            <div class="col-lg-4 mx-auto">
              <div class="auto-form-wrapper">
                <form @submit="auth">
                  <div class="form-group text-center">
                    <img class="mr-3" src="../../assets/images/header_logo.png" style="height:150px;" alt="">
                  </div>
                  <div class="form-group">
                    <label class="label">Username</label>
                    <input type="text" class="form-control" v-model="username" placeholder="Username">
                  </div>
                  <div class="form-group">
                    <label class="label">Password</label>
                    <input type="password" class="form-control" v-model="password" placeholder="Password">
                  </div>
                  <div class="form-group">
                    <button class="btn btn-primary submit-btn btn-block">Login</button>
                  </div>
                </form>
              </div>
              <p class="footer-text text-center" style="margin-top: 5px;"> 2024 SRS Peddler. All rights reserved.</p>
            </div>
          </div>
        </div>
        <!-- content-wrapper ends -->
      </div>
      <!-- page-body-wrapper ends -->
    </div>
  </section>
</template>

<script lang="js">
/* eslint-disable */
// import { mapMutations, mapState } from 'vuex'
export default {
  name: 'login',
  data() {
    return {
      username: '',
      password: '',
    }
  },
  // computed: mapState({ user: state => state.user }),
  methods: {
    // ...mapMutations(['setUser', 'setToken']),
    async auth(e) {
      try {
        e.preventDefault()
        let headers = {
          "Content-Type": "application/json",
        }
        let data = {
          username: this.username,
          password: this.password
        }
        let row = await this.$axios.post(this.$apiUrl + "login", JSON.stringify(data), { headers })
        let res = row.data
        if (res.status == 'ok') {
          localStorage.setItem('token', `${res.token}`)
          localStorage.setItem('account_no', `${res.account_no}`)
          localStorage.setItem('branch', `${res.branch}`)
          localStorage.setItem('location_id', `${res.user_id}`)
          localStorage.setItem('location_name', `${res.branch}`)
          localStorage.setItem('user_role', `${res.user_role}`)
          localStorage.setItem('user_id', `${res.user_id}`)
          localStorage.setItem('user_firstname', `${res.user_firstname}`)
          localStorage.setItem('user_lastname', `${res.user_lastname}`)
          this.$swal('Success!', 'You successfully login')
          this.$router.push({ name: 'products' })
        } else {
          this.$swal('Error!', 'Invalid username or password', 'error')
        }
      } catch (e) {
        this.$swal('Error!', 'Invalid username or password', 'error')
      }
      // this.setUser(res.user_id)
      // this.setToken(res.token)
      // await this.setAxiosHeaders(res.token)
    },
    // async setAxiosHeaders(token) {
    //   this.$axios.defaults.headers.common['Authorization'] = `Bearer ${token}`
    // }
  }

}
</script>

<style scoped lang="scss">
.login {}
</style>



