<template lang="html">
    <section class="orders">
        <div class="col-lg-12 grid-margin stretch-card">
          <div class="card">
            <div class="card-body" style="padding-left: 15px; padding-right: 15px;">
                <h3 class="card-title">List of Out of Stocks</h3>
              <div class="table-responsive">
                <div class="form-group">
                    <input type="text" class="form-control" placeholder="Search Products" v-model="search" v-on:keyup="get_products()">
                </div>
                <table class="table text-center">
                    <thead>
                        <tr>
                            <th>Product ID</th>
                            <th>Product Name</th>
                            <th>Product Price</th>
                            <th>Product Stock</th>
                            <th>Product Status</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-show="loader_spinner">
                            <th colspan="5" class="text-center">
                                <span class="spinner-border" role="status"></span>
                            </th>
                        </tr>
                        <tr v-show="!loader_spinner" v-for="(row,i) in list_products" :key="i">
                            <td>{{ row.product_id }}</td>
                            <td>{{ row.product_name }}</td>
                            <td>{{ row.product_price }}</td>
                            <td>{{ row.product_stock }}</td>
                            <td>{{ (row.product_stock != null) ? 'Published' : 'Not Published' }}</td>
                        </tr>
                    </tbody>
                    <tfoot>
                        <tr>
                            <td class="page text-center" colspan="5" align="center">
                                <paginate
                                :pageCount="page_count"
                                :container-class="'pagination text-center'"
                                page-class="page-item"
                                prev-link-class="page-link"
                                next-link-class="page-link"
                                page-link-class="page-link"
                                prev-text="PREV"
                                next-text="NEXT"
                                :click-handler="get_products"
                                ></paginate>
                            </td>
                        </tr>
                    </tfoot>
                </table>
              </div>
            </div>
          </div>
        </div>
    </section>
  </template>
<script lang="js">
/* eslint-disable */
  export default {
    name: 'list_oos',
    data () {
      return {
        search: '',
        list_products: [],
        page_count: 0,
        prev: 'PREV',
        next: 'NEXT',
        currentSort: 'id',
        currentSortDir: 'desc',
        pagination: 'pagination',
        loader_spinner: true
      }
    },
    methods: {
        async get_products(page=1) {
            this.loader_spinner = true
            try {
                let query = {
                    p_search: this.search,
                    p_page: page,
                    p_currentSort: this.currentSort,
                    p_currentSortDir: this.currentSortDir
                }

                let result = await this.$axios.post(this.$apiUrl+"get_products_oos", query)
                let res = result.data
                this.list_products = res.res
                this.page_count = parseInt(res.page_count)
            } catch(e) {
                console.log(e)
            }
            this.loader_spinner = false
        }
    },
    async mounted() {
        await this.get_products()
    }
  }
  </script>
  
  <style scoped lang="scss">
  .pagination {
        display: flex;
        justify-content: center;
    }
    #orderModal {
        z-index: 10000000 !important;
    }
  </style>
  


