// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
/* eslint-disable */
import Vue from "vue";
import App from "./App";
import router from "./router";
import store from "./store";
import BootstrapVue from "bootstrap-vue";
import JQuery, { proxy } from "jquery";
import accounting from "accounting";
import VueSlideBar from "vue-slide-bar";
import VueSweetalert2 from "vue-sweetalert2";
import VueGoodWizard from "vue-good-wizard";
import axios from "axios";
import pagination from "vue-pagination-bootstrap";
import moment from "moment";
import paginate from "vuejs-paginate";
import VModal from "vue-js-modal";
import VSelect from "vue-select";
import "vue-select/dist/vue-select.css";
// import { HttpsProxyAgent } from "https-proxy-agent";

let $ = JQuery;

const options = {
  confirmButtonColor: "#41b882",
  cancelButtonColor: "#ff7674",
};

Vue.use(BootstrapVue);
Vue.use(VueSweetalert2, options);
Vue.use(VueGoodWizard);
Vue.use(VModal);
// Vue.use(paginate)

Vue.component("vue-slide-bar", VueSlideBar);
Vue.component("pagination", pagination);
Vue.component("paginate", paginate);
Vue.component("v-select", VSelect);
Vue.config.productionTip = false;

// const proxyUrl = "http://192.168.0.203:80";
// const proxyAgent = new HttpsProxyAgent(proxyUrl);
// axios.defaults.httpAgent = proxyAgent;
// axios.defaults.httpsAgent = proxyAgent;
Vue.prototype.$axios = axios;

Vue.prototype.$apiUrl = "/api/";

Vue.filter("laymandate", function (value) {
  return moment(value).format("MMMM-DD-YYYY");
});

Vue.filter("hoursago", function (value) {
  return moment(value).fromNow();
});

Vue.filter("wpstatus", function (value) {
  let temp = value.substring(3);
  let finalStr = temp.toLowerCase().split(" ");
  for (let i = 0; i < finalStr.length; i++) {
    finalStr[i] =
      finalStr[i].charAt(0).toUpperCase() + finalStr[i].substring(1);
  }
  return finalStr.join(" ");
});

Vue.filter("money", function (money) {
  return accounting.formatNumber(money, 3, ",");
});

/* eslint-disable no-new */
new Vue({
  el: "#app",
  router,
  store,
  components: {
    App,
  },
  template: "<App/>",
});



